<template>
  <div class="loving">
    <div>
      {{ dateTime }}
    </div>
    <h2>loving {{loveDuration}} days</h2>
    <p>love will be back ...</p>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      msg: new Date(),
      currTime: new Date(),
      loveTime: new Date(2016, 5, 20)
    }
  },
  computed: {
    dateTime () {
      return this.dateFormat(this.currTime)
    },
    loveDuration () {
      var duration = this.currTime - this.loveTime
      return Math.floor(duration / (24 * 3600 * 1000))
    }
  },
  methods: {
    zeroPadding (num, digit) {
      var zero = ''
      for (var i = 0; i < digit; i++) {
        zero += '0'
      }
      return (zero + num).slice(-digit)
    },
    dateFormat (time) {
      return this.zeroPadding(time.getFullYear(), 4) +
      '-' + this.zeroPadding(time.getMonth() + 1, 2) +
      '-' + this.zeroPadding(time.getDate(), 2) +
      ' ' + this.zeroPadding(time.getHours(), 2) +
      ':' + this.zeroPadding(time.getMinutes(), 2) +
      ':' + this.zeroPadding(time.getSeconds(), 2)
    }
  },
  created () {
    setInterval(() => {
      this.currTime = new Date()
    }, 1000)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.loving {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  text-align: center;
}
</style>



